import React, { useState } from 'react'
import { Button, message, Form, Input, Divider, InputNumber } from 'antd';
import "./UploadPage.scss"
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

export default function UploadPage() {
    const [imageUrl, setImageUrl] = useState(null);
    const history = useNavigate();
    const onSubmit = (values) => {
        axios.post(``,{
            name: values.name,
            description: values.description,
            seller : values.seller,
            price: values.price,
            imageUrl: imageUrl
        }).then((result)=>{
            console.log(result);
            history('/',{replace:true})
        }).catch((error)=>{
            console.log(error);
            message.error(`  ${error}`)
        })
    };
    const onChangeImage = (info) => {
        // if(){
        //     return;
        // }
        // if(){

        // }
    }
  return (
    <div id='upload-container'>
      <Form name='upload-form' onFinish={onSubmit}>
        <Form.Item name="upload" label={<span className='upload-label'></span>}>
            <div id='upload-image' onChange={onChangeImage}>
                <img src="/images/icons/camera.png" alt="" />
                <span>  </span>
            </div>
        </Form.Item>
        <Divider/>
        <Form.Item name='seller' rules={[{required:true, message: "   "}]} label={<span className='upload-label'></span>}>
            <Input className='upload-name' placeholder=' ' size='large'/>
        </Form.Item>
        <Divider/>
        <Form.Item name='name' rules={[{required:true, message: "   "}]} label={<span className='upload-label'></span>}>
            <Input className='upload-name' placeholder=' ' size='large'/>
        </Form.Item>
        <Divider/>
        <Form.Item name='price' rules={[{required:true, message: "   "}]} label={<span className='upload-label'></span>}>
            <InputNumber className='upload-price' min={1} size='large'/>
        </Form.Item>
        <Divider/>
        <Form.Item name='description' rules={[{required:true, message: "   "}]} label={<span className='upload-label'></span>}>
            <Input.TextArea id='product-description' showCount maxLength={300} placeholder="  " size='large'/>
        </Form.Item>
        <Divider/>
        <Form.Item>
            <Button id='submit-button' size='large' htmlType='submit'></Button>
        </Form.Item>
      </Form>
    </div>
  )
}